import { Form } from './common/Form';
import { ensureElement } from '../utils/utils';
export class OrderForm extends Form {
    constructor(container, events) {
        super(container, events);
        this._card = ensureElement('button[name=card]', container);
        this._cash = ensureElement('button[name=cash]', container);
        this._card.addEventListener('click', (e) => {
            const target = e.target;
            const field = 'payment';
            const value = target.name;
            this.onInputChange(field, value);
        });
        this._cash.addEventListener('click', (e) => {
            const target = e.target;
            const field = 'payment';
            const value = target.name;
            this.onInputChange(field, value);
        });
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    onInputChange(field, value) {
        this.events.emit(`order.${field}:change`, {
            field,
            value,
        });
    }
    set payment(value) {
        this.toggleClass(this.container.elements.namedItem('card'), 'button_alt', this.container.elements.namedItem('card').name !==
            value);
        this.toggleClass(this.container.elements.namedItem('cash'), 'button_alt', this.container.elements.namedItem('cash').name !==
            value);
    }
}
